.App {
  text-align: center;
  width: 60%;
  margin: auto;
}

/* CUSTOM STYLES */
body {
  background-color: rgb(241, 241, 241);
}

h1 {
  margin: 30px;
  font-size: 40px;
}

.row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
}

.cardDiv {
  width: 35%;
  border: 1px solid black;
  border-radius: 5px;
  -webkit-box-shadow: 0 2px 3px #bbb;
          box-shadow: 0 2px 3px #bbb;
  padding: 16px;
  background-color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  margin: 5px, 0;
}

.cardDiv h2 {
  margin: 0;
}

hr {
  border: 1px solid #ddd;
  width: 100%;
}

.UserForm form {
  text-align: left;
}

.UserForm .form-input {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

.UserForm label {
  font-weight: bold;
}

.UserForm input, .UserForm select {
  width: 60%;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ddd;
  margin: 5px;
  font-size: 18px;
}

.imgDiv {
 position: relative;
 overflow: hidden;
 border-radius: 50%;
 border: 2px solid black;
 width: 180px;
 height: 180px;
}

.UserProfile img {
  height: 180px;
  width: auto;
  position: absolute;
  top: -9999px;
  bottom: -9999px;
  left: -9999px;
  right: -9999px;
  margin: auto;
}

.UserProfile h2 {
  margin: 10px;
}

.UserProfile p {
  margin: 0;
}

.bio {
  border: 1px solid black;
  border-radius: 5px;
  padding: 10px;
  font-size: 18px;
}

.redProfile {
  background-color: rgb(253, 184, 184);
  border: 2px solid red;
}

.greenProfile {
  background-color: rgb(195, 253, 184);
  border: 2px solid green;
}

.blueProfile {
  background-color: rgb(184, 191, 253);
  border: 2px solid blue;
}

.submitButton {
  width: 100%;
  font-size: 24px;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid black;
  margin-top: 5px;
}

.submitButton:hover {
  cursor: pointer;
  background-color: #bbb;
}

.profile {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin: 10px;
}

.profilesRow {
  margin-bottom: 50%;
}

.createProfileButton button {
  background-color: white;
}


